
.loader {
    /*свг и/или анимация*/
    width: 50px;
    height: 50px;
    border: 5px solid #ccc;
    border-top-color: #3498db; /* Цвет вращающегося сегмента */
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

